// Step 2: Services
.booking-service-categories {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  gap: 8px;
  margin-bottom: 20px;
  padding-bottom: 10px;

  &::-webkit-scrollbar {
    height: 4px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(253, 196, 31, 0.7);
    border-radius: 10px;
  }
}

.category-tab {
  display: flex;
  align-content: JCC;
  white-space: nowrap;
  padding: 10px 20px;
  border-radius: 50px;
  border: 1px solid var(--black, #302f34);
  color: #302f34;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.32px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    transition: all 0.2s ease;
    background: #fdc41f;
  }

  &.active {
    border: none;
    background: #fdc41f;
  }
}

.services-list {
  max-height: 400px;
  overflow-y: auto;
  max-width: 825px;
  padding: 30px;

  border-radius: 10px;
  background: rgba(255, 255, 255, 0.75);
  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(253, 196, 31, 0.7);
    border-radius: 10px;
  }
}
.category-services {
  display: flex;
  flex-direction: column;
  gap: 31px;
}
.service-item {
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
}

.service-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  color: #302f34;
  font-size: 18px;
  font-weight: 700;
  line-height: 110%; /* 19.8px */
}

.service-description,
.service-duration,
.service-wear-time {
  margin-bottom: 4px;
  color: var(--black, #302f34);
  font-size: 14px;
  font-weight: 400;
  line-height: 140%; /* 19.6px */

  strong {
    font-weight: 700;
  }
}

.service-checkbox-wrapper {
  display: flex;
  align-items: center;
  gap: 24px;
}

.service-checkbox {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 24px;
  height: 24px;
  border: 2px solid #ccc;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  position: relative;
  background-color: #fff;

  &:checked {
    background-color: #fdc41f;
    border-color: #fdc41f;

    &:after {
      content: "✓";
      position: absolute;
      color: #fff;
      font-size: 16px;
      font-weight: bold;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &:hover {
    border-color: #fdc41f;
  }
}
