.btn {
  padding: 1.3020833333vw 1.6276041667vw;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 0.9114583333vw;
  line-height: 100%;
  border: 0.0651041667vw solid #302f34;
  border-radius: 100px;
  transition: background 0.3s, border-color 0.3s, color 0.3s;
  cursor: pointer;
  white-space: nowrap;
}
@media (max-width: 1024px) {
  .btn {
    padding: 1.953125vw 2.44140625vw;
    font-size: 1.3671875vw;
    border-width: 0.09765625vw;
  }
}
@media (max-width: 768px) {
  .btn {
    padding: 14px 24px;
    font-size: 12px;
    border-width: 1px;
  }
}
.btn:hover {
  background: #f1e3be;
}
.btn.white {
  background: #fff;
  border-color: #fff;
  padding-right: 3.6458333333vw;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .btn.white {
    padding-right: 7.03125vw;
  }
}
@media (max-width: 768px) {
  .btn.white {
    padding-right: 72px;
  }
}
.btn.white::before {
  position: absolute;
  height: calc(100% - 0.5208333333vw);
  background: url("../svg/circle.svg") center/cover no-repeat;
  content: "";
  aspect-ratio: 1/1;
  right: 0.2604166667vw;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.5s;
}
@media (max-width: 1024px) {
  .btn.white::before {
    right: 0.390625vw;
    height: calc(100% - 0.78125vw);
  }
}
@media (max-width: 768px) {
  .btn.white::before {
    right: 2px;
    height: calc(100% - 4px);
  }
}
.btn.white:hover {
  background: #f1e3be;
  border-color: #f1e3be;
}
.btn.white:hover::before {
  transform: translateY(-50%) rotate(45deg);
}
.btn.yellow {
  background: #fdc41f;
  border-color: #fdc41f;
  padding-right: 3.6458333333vw;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .btn.yellow {
    padding-right: 7.03125vw;
  }
}
@media (max-width: 768px) {
  .btn.yellow {
    padding-right: 72px;
  }
}
.btn.yellow::before {
  position: absolute;
