.burger {
  position: relative;
  width: 22px;
  cursor: pointer;
  height: 16px;
  z-index: 102;
  display: none;
}
@media (max-width: 768px) {
  .burger {
    display: block;
  }
}
.burger::before {
  content: "";
  width: 36px;
  height: 36px;
  background: #fdc41f;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bar {
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background: #000;
  color: inherit;
  opacity: 1;
  transition: none 0.35s cubic-bezier(0.5, -0.35, 0.35, 1.5) 0s;
}

.bar--top {
  bottom: calc(50% + 5px + 1px);
  transition-property: bottom, transform;
  transition-delay: 0.35s, 0s;
}

.bar--middle {
  top: calc(50% - 1px);
  transition-property: opacity;
  transition-delay: 0.35s;
}

.bar--bottom {
  top: calc(50% + 5px + 1px);
  transition-property: top, transform;
  transition-delay: 0.35s, 0s;
}

.burger.active .bar--top {
  bottom: calc(50% - 1px);
  transform: rotate(135deg);
  transition-delay: 0s, 0.35s;
}

.burger.active .bar--middle {
  opacity: 0;
  transition-duration: 0s;
  transition-delay: 0.35s;
}

.burger.active .bar--bottom {
  top: calc(50% - 1px);
  transform: rotate(225deg);
  transition-delay: 0s, 0.35s;
}

