@media (max-width: 1024px) {
  .icons {
    gap: 0.78125vw;
  }
}
@media (max-width: 768px) {
  .icons {
    gap: 4px;
  }
}
.icons a {
  display: inline-block;
  transition: transform 0.5s;
}
.icons a:hover {
  transform: scale(1.2);
}
.icons img {
  width: 2.34375vw;
  height: 2.34375vw;
}
@media (max-width: 1024px) {
  .icons img {
    width: 3.515625vw;
    height: 3.515625vw;
  }
}
@media (max-width: 768px) {
  .icons img {
    width: 40px;
    height: 40px;
  }
}